<template>
  <div class="view-container">
    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!--  -->
    <div class="row ope-container">
      <el-button type="primary" icon="el-icon-refresh" size="small" @click="refreshPermissionList()"></el-button>
      <el-button type="success" size="small" @click="addPermission()"></el-button>
      <Search v-on:search="searchPermissionData()"></Search>
    </div>

    <!--  -->
    <div class="row data-container">
      <el-table :data="permissionData" border style="width: 100%" :max-height="tabMaxHeight">
        <el-table-column label="ID" prop="id">
        </el-table-column>
        <el-table-column label="" prop="code">
        </el-table-column>
        <el-table-column label="" prop="name">
        </el-table-column>
        <el-table-column label="" prop="createTime">
        </el-table-column>
        <el-table-column label="" prop="updateTime">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="danger" size="mini" @click="delPermissionItem(scope.row)">
              <i class="el-icon-delete"></i>
            </el-button>
            <el-button type="primary" size="mini" @click="editPermissionItem(scope.row)">
              <i class="el-icon-edit"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="row page-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalSize">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="addPermissionDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="80px" :model="addPermissionData" ref="addPermissionData">
        <el-form-item label="" prop="code">
          <el-input v-model="addPermissionData.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="addPermissionData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="action">
          <el-input v-model="addPermissionData.action"></el-input>
        </el-form-item>
        <el-form-item label="" prop="menuId">
          <el-select v-model="addPermissionData.menuId" placeholder="">
            <el-option v-for="item in allLeafData" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="idOnPage">
          <el-input v-model="addPermissionData.idOnPage"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addPermissionDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitAddPermission('addPermissionData')"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delPermissionDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="delPermissionDialogVisible = false"> </el-button>
        <el-button type="danger" size="small" @click="submitDelPermission()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editPermissionDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="80px" :model="editPermissionData" ref="editPermissionData">
        <el-form-item label="" prop="code">
          <el-input v-model="editPermissionData.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editPermissionData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="action">
          <el-input v-model="editPermissionData.action"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editPermissionDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitEditPermission('editPermissionData')"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>
<script>
import Search from './Commen/Search.vue'
export default {
  name: 'permission',
  data() {
    return {
      // 
      addPermissionDialogVisible: false,
      // 
      addPermissionData: {
        code: '',
        name: '',
        action: '',
        menuId: '',
        idOnPage: ''
      },
      //  
      delPermissionDialogVisible: false,
      // id
      delPermissionId: 0,
      //  
      editPermissionDialogVisible: false,
      //  
      editPermissionData: {
        id: '',
        code: '',
        name: '',
        action: ''
      },
      //
      currentPage: 1,
      pageSize: 20,
      totalSize: 0,
      // 
      permissionData: [],
      // 
      allLeafData: [],
      // 
      tabMaxHeight: 0
    }
  },
  computed: {
    searchId() {
      return this.$store.state.user.searchId
    }
  },
  components: {
    Search
  },
  mounted() {
    // 
    this.getPermissionData()
  },
  methods: {
    //  
    getPermissionData() {
      this.$store
        .dispatch('getPermissionData', {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          name: this.searchId
        })
        .then(res => {
          this.permissionData = res.data.list
          this.totalSize = res.data.total
          let clientHieght = document.body.clientHeight
          this.tabMaxHeight = clientHieght - 60 - 30 - 30 - 50 - 50
        })
    },
    // 
    refreshPermissionList() {
      this.getPermissionData()
    },
    // 
    searchPermissionData() {
      this.getPermissionData()
    },
    //  
    addPermission() {
      this.addPermissionDialogVisible = true
      this.$store.dispatch('getAllLeaf').then(res => {
        console.log('[allLeafData]', res.data)
        this.allLeafData = res.data
      })
    },
    // 
    submitAddPermission(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.addPermissionDialogVisible = false
          this.$store.dispatch('addPermissionData', this.addPermissionData).then(res => {
            this.getPermissionData()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    delPermissionItem(row) {
      this.delPermissionId = row.id
      this.delPermissionDialogVisible = true
    },
    // 
    submitDelPermission() {
      this.$store.dispatch('delPermissionData', this.delPermissionId).then(res => {
        this.getPermissionData()
      })
      this.delPermissionDialogVisible = false
    },
    // 
    editPermissionItem(row) {
      this.editPermissionData.id = row.id
      this.editPermissionData.code = row.code
      this.editPermissionData.name = row.name
      this.editPermissionData.action = row.action
      this.editPermissionDialogVisible = true
      this.$store.dispatch('getAllLeaf').then(res => {
        console.log('[allLeafData]', res.data)
        this.allLeafData = res.data
      })
    },
    // 
    submitEditPermission() {
      this.$store.dispatch('updatePermissionData', this.editPermissionData).then(res => {
        this.getPermissionData()
      })
      this.editPermissionDialogVisible = false
    },
    // 
    handleClose() {
      this.addPermissionDialogVisible = false
      this.delPermissionDialogVisible = false
      this.editPermissionDialogVisible = false
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.pageSize = val
      this.getPermissionData()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getPermissionData()
    }
  },
  watch: {
    addPermissionDialogVisible() {
      if (!this.addPermissionDialogVisible) {
        this.addPermissionData = {
          code: '',
          name: '',
          action: '',
          menuId: '',
          idOnPage: ''
        }
      }
    }
  }
}
</script>
<style>
.view-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.breadcrumb-container {
  height: 20px;
}
.ope-container {
  height: 40px;
}
.data-container {
  flex: 1;
  overflow: auto;
  display: flex;
}
.page-container {
  height: 40px;
}
</style>




